<template>
  <div id="report-setting-page">
    <div class="wrapper">
      <el-tabs v-model="curView" @tab-click="">
        <el-tab-pane label="区块布局" name="1">
          <div class="tab-content df dfv">
            <div class="query-form df">
              <el-form :inline="true" :label-width="'100px'" style="max-width: 1300px;">
                <el-form-item :label="'页面名称'" prop="name">
                  <el-input size="mini" v-model="layoutQuery.name"></el-input>
                </el-form-item>
              </el-form>
            </div>

            <div class="list df dfv">
              <div class="list-header">
                <h3>页面层级菜单列表</h3>
              </div>
            </div>
            <div id="layout-tree"></div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="报表配置" name="2">
          <div class="tab-content df dfv">
            <div class="query-form df">
              <el-form :inline="true" :label-width="'100px'" style="max-width: 1300px;">
                <el-form-item :label="'任务编号'" prop="name">
                  <el-input size="mini" v-model="layoutQuery.name"></el-input>
                </el-form-item>
                <el-form-item :label="'任务类型'" prop="name">
                  <el-input size="mini" v-model="layoutQuery.name"></el-input>
                </el-form-item>
                <el-form-item :label="'报表名称'" prop="name">
                  <el-input size="mini" v-model="layoutQuery.name"></el-input>
                </el-form-item>
                <el-form-item :label="'报表编号'" prop="name">
                  <el-input size="mini" v-model="layoutQuery.name"></el-input>
                </el-form-item>
                <el-form-item :label="'报表类型'" prop="name">
                  <el-input size="mini" v-model="layoutQuery.name"></el-input>
                </el-form-item>


                <el-button type="primary" size="mini" @click="" style="margin-left: 33px;">确认</el-button>
                <el-button type="primary" size="mini" @click="">清除</el-button>
              </el-form>
            </div>
            <div class="list df dfv">
              <div class="list-header">
                <h3>活动告警列表</h3>
              </div>
              <div class="table-box df dfv">

                <el-table height="100%" :data="configList">
                  <el-table-column label="任务编号" prop="enterpriseName"></el-table-column>
                  <el-table-column label="报表编号" prop="serverName"></el-table-column>
                  <el-table-column label="报表名称" prop="deviceType"></el-table-column>
                  <el-table-column label="报表类型" prop="generate"></el-table-column>
                  <el-table-column label="任务类型" prop="operator.name" :formatter="$tableFilter('nullFilter')"></el-table-column>
                  <el-table-column label="任务状态" prop="generate"></el-table-column>
                  <el-table-column label="有效期" prop="generate"></el-table-column>
                  <el-table-column label="接口" prop="generate"></el-table-column>
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <a @click="">详情</a>
                      <a @click="setReportDetailToModal(scope.row)">修改</a>
                    </template>
                  </el-table-column>
                </el-table>
                <!-- Pagination -->
                <el-col :span="24" class="toolbar">
                  
                  <el-pagination
                    layout="total, prev, pager, next,sizes, jumper"
                    @current-change="pagination.handleCurrentChange"
                    @size-change="pagination.pageSizeChange"
                    :page-sizes="[10, 15, 50, 100]"
                    :page-size="pagination.pageSize"
                    :total="pagination.total"
                    style="float:right;">
                  </el-pagination>
                </el-col>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>

    <el-dialog
      title="报表任务修改"
      class="m400"
      :close-on-click-modal = "false"
      @close="setReportDetailVisible = false"
      :visible="setReportDetailVisible">
      <el-form :model="setReportDetailForm" :rules="setReportDetailRules" label-width="100px" ref="setReportDetail" >
        <el-form-item label="报表名称" prop="machineType">
          <span>{{ setReportDetailForm.enterpriseName }}</span>
        </el-form-item>
        <el-form-item label="报表编号" prop="machineType">
          <span>{{ setReportDetailForm.serverName }}</span>
        </el-form-item>
        <el-form-item label="任务类型" prop="machineType">
          <el-select v-model="setReportDetailForm.sdfas" size="mini" style="width: 240px">
            <el-option v-for="(item, index) in []" :key="index" :value="item" :label="item">{{ item }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="有效期" prop="aaa">
          <el-select v-model="setReportDetailForm.sdfas" size="mini" style="width: 240px">
            <el-option v-for="(item, index) in []" :key="index" :value="item" :label="item">{{ item }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="统计周期" prop="aaa">
          <el-select v-model="setReportDetailForm.sdfas" size="mini" style="width: 240px">
            <el-option v-for="(item, index) in []" :key="index" :value="item" :label="item">{{ item }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="接口" prop="aaa">
          <el-select v-model="setReportDetailForm.sdfas" size="mini" style="width: 240px">
            <el-option v-for="(item, index) in []" :key="index" :value="item" :label="item">{{ item }}</el-option>
          </el-select>
        </el-form-item>

        <el-form-item :label="setReportDetailForm.sourceLevelList[index] + '级告警'"  :prop="'relation' + index" v-for='(item ,index) in setReportDetailForm.sourceLevelList' :key="index">
          <el-select size="mini" v-model="setReportDetailForm.targetLevelList[index]" style="width:190px">
            <el-option v-for="(item, index) in (setReportDetailForm.targetLevelOptions || [])" :key="index" :label="item.levelName" :value="item.level">{{ item.levelName }}</el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="setReportDetailVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="setReportDetailSubmit();">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      curView: '1',
      layoutQuery: [],
      layoutTree: {},
      configList: [{}, {}],
      pagination: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.pagination.page = v;
          this.getConfigList();
        },
        pageSizeChange: () => {
          this.pagination.pageSize = v;
          this.getConfigList();
        },
      },
      setReportDetailVisible: false,
      setReportDetailForm: {
        
      },
    }
  },
  computed: {
    setReportDetailRules () {
      return {}
    }
  },
  methods: {
    getConfigList () {

    },
    setReportDetailToModal (report) {
      this.setReportDetailVisible = true;
      this.$nextTick(() => {
        this.setReportDetailForm = {};
      })
    },
    setReportDetailSubmit () {

    },
  },
  mounted () {

  }
}
</script>

<style lang="less" scoped>
#report-setting-tree{
  background-color: #011838;
}
.wrapper{
  height: 100%;
}
.el-tabs{
  flex: 1;
  height: calc(100% - 20px);
  margin: 10px;
  background-color: #0B1D3B;
}
.list{
  flex: 1;
  overflow: hidden;
  padding-bottom: 10px;
  .table-box{
    flex: 1;
    overflow: hidden;
  }    
}
</style>


